import { Edit } from "@mui/icons-material";
import { IconButton, Rating } from "@mui/material";
import { Fragment, useState } from "react";
import Delete from "icons/Delete";
import CreateClasificacionModal from "./create-clasificacion";
const ClasificacionColumns = [
    {
        Header: "#",
        accessor: "id"
    },
    {
        Header: "Nombre",
        accessor: "nombreClasificacion"
    },
    {
        Header: "Pertenece a:",
        accessor: "nombreClasificacionPadre"
    },
    {
        Header: "Acciones",
        accessor: "acciones",
        Cell: ({
            row
        }) => {
            const style = {
                fontSize: 19,
                transition: "color 0.3s",
                color: row.isSelected ? "white" : "text.disabled"
            };
            const [tipo, setTipo] = useState('');
            const [openModal, setOpenModal] = useState(false);
            return <Fragment>
                <IconButton onClick={() => {
                    setOpenModal(true)
                    setTipo('editar')
                }}>
                    <Edit sx={{
                        fontSize: 18,
                        color: "text.disabled"
                    }} />
                </IconButton>
                <IconButton onClick={() => { alert('funcion pendiente') }}>
                    <Delete sx={style} />
                </IconButton>
                <CreateClasificacionModal
                    editProduct
                    open={openModal}
                    data={row.original}
                    id={row.original.id}
                    onClose={
                        () => {
                            setOpenModal(false)
                            setTipo('')
                        }
                    }
                    tipo={tipo} />
            </Fragment>;
        }
    }];
export default ClasificacionColumns;