
import { Autocomplete, Box, Button, Grid, IconButton, styled, TextField, useMediaQuery } from "@mui/material";
import AppModal from "components/AppModal";
import FlexBox from "components/flexbox/FlexBox";
import FlexRowAlign from "components/flexbox/FlexRowAlign";
import AppTextField from "components/input-fields/AppTextField";
import Scrollbar from "components/ScrollBar";
import { H2, H6, Small } from "components/Typography";
import { Context } from "contexts/ContextDataTable";
import { useFormik } from "formik";
import { useContext, useEffect, useLayoutEffect, useState } from "react";
import { CrearClasificacionService, EditarClasificacionService, GuardarClasificacionService, ModificarClasificacionService } from "Services/api-ventas-erp/clasificacionService";
import * as Yup from "yup"; // component props interface

// styled components
const StyledAppModal = styled(AppModal)(({
    theme
}) => ({
    maxWidth: 700,
    minWidth: 300,
    outline: "none",
    padding: "1.5rem"
}));

const CreateClasificacionModal = ({
    open,
    data,
    onClose,
    editProduct,
    tipo,
    id
}) => {
    const downXl = useMediaQuery(theme => theme.breakpoints.down("xl"));
    const [context, setContext] = useContext(Context);
    const initialState = {
        id: 0,
        nombreClasificacion: '',
        clasificacionId: 0,
        nombreClasificacionPadre: ''
    };

    const [options, setOptions] = useState([])
    const validationSchema = Yup.object().shape({
        id: Yup.number().nullable(),
        nombreClasificacion: Yup.string().min(3, "Es muy corto").required("Nombre es requerido!"),
        clasificacionId: Yup.string().nullable(),
        nombreClasificacionPadre: Yup.string().nullable(),
    });

    const {
        values,
        errors,
        handleChange,
        handleSubmit,
        touched,
        setFieldValue,
        resetForm,
        setValues
    } = useFormik({
        initialValues: initialState,
        validationSchema,
        onSubmit: values => {
            switch (tipo) {
                case 'nuevo':
                    apiGuardarClasificacion()
                    break;
                case 'editar':
                    apiModificarClasificacion()
                    break;
                default:
                    break;
            }
        }
    });
    const apiCreateClasificacion = async () => {
        const { data } = await CrearClasificacionService();
        setOptions(data.data.clasificacion);
    }
    const apiGuardarClasificacion = async () => {
        const { data } = await GuardarClasificacionService(values);
        resetForm();
        onClose();
        setContext(true);
    }
    const apiEditarClasificacion = async () => {
        const { data } = await EditarClasificacionService(id);
        setOptions(data.data.clasificaciones);
        setValues({ ...data.data.clasificacion })
    }
    const apiModificarClasificacion = async () => {
        console.log(id,values.id )
        const { data } = await ModificarClasificacionService(values);
        resetForm();
        onClose();
        setContext(true)
    }
    useEffect(() => {

        switch (tipo) {
            case 'nuevo':
                apiCreateClasificacion()
                break;
            case 'editar':
                apiEditarClasificacion()
                break;
            case 'eliminar':

                break;
            default:
                break;
        }

    }, [open])
    return <StyledAppModal open={open} handleClose={onClose}>
        <H2 marginBottom={2}>
            {editProduct && data ? "Editar Clasificacion" : "Añadir Clasificacion"}
        </H2>

        <form onSubmit={handleSubmit}>
            <Scrollbar style={{
                maxHeight: downXl ? 500 : "auto"
            }}>
                <Grid container spacing={2}>
                    <Grid item sm={12} xs={12}>
                        <H6 mb={1}>Pertenece a</H6>
                        <Autocomplete
                            disablePortal
                            id="combo-box-demo"
                            getOptionLabel={(options) => options.nombreClasificacion}
                            options={options}
                            size="small"
                            onChange={(event, newValue) => {
                                if (newValue != null) {
                                    setFieldValue('clasificacionId', newValue.id)
                                } else {
                                    setFieldValue('clasificacionId', initialState.id)
                                }
                            }}
                            renderInput={
                                (params) => <TextField
                                    {...params}
                                    value={values.clasificacionId}
                                    label="Pertenece a"
                                    error={Boolean(touched.clasificacionId && errors.clasificacionId)}
                                    helperText={touched.clasificacionId && errors.clasificacionId}
                                />
                            }
                        />
                    </Grid>
                    <Grid item sm={12} xs={12}>
                        <H6 mb={1}>Nombre</H6>
                        <AppTextField
                            fullWidth
                            size="small"
                            name="nombreClasificacion"
                            placeholder="Nombre"
                            value={values.nombreClasificacion}
                            onChange={handleChange}
                            error={Boolean(touched.nombreClasificacion && errors.nombreClasificacion)}
                            helperText={touched.nombreClasificacion && errors.nombreClasificacion}
                        />
                    </Grid>
                </Grid>
            </Scrollbar>

            <Grid container>
                <Grid item xs={12}>
                    <FlexBox justifyContent="flex-end" gap={2} marginTop={2}>
                        <Button fullWidth variant="outlined" onClick={onClose}>
                            Cancel
                        </Button>
                        <Button fullWidth type="submit" variant="contained">
                            Guardar
                        </Button>
                    </FlexBox>
                </Grid>
            </Grid>
        </form>
    </StyledAppModal>;
};

const images = ["/static/products/watch.png", "/static/products/camera.png", "/static/products/headphone.png"];
export default CreateClasificacionModal;